.hiro-artwork-form-control {
	.hiro-artwork-boolean-switch {
		display: inline-flex;
		align-items: center;
		border: 1px solid #2c2c2c;
		border-radius: 8px;
		background: #333;
		overflow: hidden;

		.hiro-artwork-boolean-button {
			padding: 8px 20px;
			border: none;
			background-color: transparent;
			color: #aaa;
			width: calc(50% - 0.5px);
			cursor: pointer;
			outline: none;
			transition: all 0.3s;

			&:hover {
				background-color: #424242;
			}

			&.active {
				color: #fff;
			}
		}

		.divider {
			height: 24px;
			width: 1px;
			background-color: #d6d6d6;
		}
	}
}
