@import url('normalize.scss');

$hiro-artwork-sidebar-bg: rgb(31, 32, 46);
$hiro-artwork-label-color: white;
$hiro-artwork-subLabel-color: lightblue;
$hiro-artwork-primary-color: #005cc8;

[class^='hiro-artwork-'],
[class^='hiro-artwork-']:after,
[class^='hiro-artwork-']:before,
[id^='hiro-artwork-'],
[id^='hiro-artwork-']:after,
[id^='hiro-artwork-']:before {
	font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont,
		Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
		apple color emoji, segoe ui emoji, Segoe UI Symbol, noto color emoji;
	box-sizing: border-box;
}

[class^='hiro-artwork-'],
[id^='hiro-artwork-'] {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p {
		margin: 0;
		padding: 0;
	}
}

div#hiro-artwork-app-wrapper {
	display: flex;
	width: 100%;
	height: 100%;
}

aside#hiro-artwork-app-sidebar {
	padding: 1.5rem 2rem;
	display: flex;
	flex-direction: column;
	row-gap: 1.5rem;
	width: 300px;
	background-color: $hiro-artwork-sidebar-bg;

	> h2#hiro-artwork-sidebar-title {
		font-size: 1.4rem;
		color: $hiro-artwork-label-color;
	}

	> p#hiro-artwork-sidebar-desc {
		font-size: 12px;
		color: $hiro-artwork-subLabel-color;
	}
}

div#hiro-artwork-form-wrapper {
	padding-top: 1.5rem;
	border-top: 1px solid white;
	width: 100%;
	display: flex;
	flex-direction: column;
	row-gap: 1.5rem;
}

main#hiro-artwork-main-content {
	flex-grow: 1;
}

.hiro-artwork-form-control {
	width: 100%;
	max-width: 320px;
	display: flex;
	flex-direction: column;
	row-gap: 0.75rem;
}

label.hiro-artwork-form-control {
	cursor: pointer;
}

input.hiro-artwork-controller:not(.hiro-artwork-toggle-checkbox) {
	width: 100%;
}

input.hiro-artwork-controller[type='color'] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	width: 50px;
	height: 50px;
	background-color: transparent;
	border: none;
	cursor: pointer;

	&::-webkit-color-swatch {
		border-radius: 15px;
		border: none;
	}

	&::-moz-color-swatch {
		border-radius: 15px;
		border: none;
	}
}

div.hiro-artwork-form-control label:not(.hiro-artwork-toggle) {
	color: white;
}

.hiro-artwork-toggle-switch {
	display: inline-block;
	background: #ccc;
	border-radius: 16px;
	width: 58px;
	height: 32px;
	position: relative;
	vertical-align: middle;
	transition: background 0.25s;
}

.hiro-artwork-toggle-switch:before,
.hiro-artwork-toggle-switch:after {
	content: '';
}

.hiro-artwork-toggle-switch:before {
	display: block;
	background: linear-gradient(to bottom, #fff 0%, #eee 100%);
	border-radius: 50%;
	box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.25);
	width: 24px;
	height: 24px;
	position: absolute;
	top: 4px;
	left: 4px;
	transition: left 0.25s;
}

label.hiro-artwork-form-control:hover .hiro-artwork-toggle-switch:before {
	background: linear-gradient(to bottom, #fff 0%, #fff 100%);
	box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
}

.hiro-artwork-toggle-checkbox:checked + .hiro-artwork-toggle-switch {
	background: $hiro-artwork-primary-color;
}

.hiro-artwork-toggle-checkbox:checked + .hiro-artwork-toggle-switch:before {
	left: 30px;
}

.hiro-artwork-toggle-checkbox {
	position: absolute;
	visibility: hidden;
}

.hiro-artwork-toggle-label {
	color: $hiro-artwork-label-color;
	margin-left: 5px;
	position: relative;
	top: 2px;
}

textarea.hiro-artwork-controller {
	resize: none;
}
